import React from 'react'
import Cookies from 'universal-cookie';
import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from "react"
import blogyimg from "../Assets/images/blogyimg.png"
import CreateArticle from "../Component/CreateArticle"
import MyArticles from "../Component/MyArticles"
import LogOut from '../Component/LogOut';
import EditUser from '../pages/EditUser';
import EditArticle from '../Component/EditArticle';



export default function Dashbord() {
  const navigate = useNavigate();
  const cookies = new Cookies();
  const [userData, setUserData] = useState(null)
  const [tab, setTab] = useState(1)
  const token = cookies.get("ut")




  const logout = () => {
    cookies.remove("ut")
    navigate('/')
  }

  useEffect(() => {
    async function getUser() {
      const token = cookies.get("ut")
      try {
        const res = await fetch('http://localhost:4000/user/me', {
          method: 'POST', // or 'PUT'
          headers: {
            'Content-Type': 'application/json',
            auth: `ut ${token}`

          },
          body: JSON.stringify({})
        })
        const data = await res.json()
        setUserData(data)
      } catch (error) {

      }
    }
    getUser()
  }, [])
  console.log(userData);
  return (
    <>
      <section className="w-full h-[100vh] relative flex flex-col items-center">
        <nav className="w-full h-[70px] bg-[#0000003f] rounded-b-[10px] absolute top-0 z-20 flex justify-between px-[80px] ">
          <div className="w-[50%] h-[70px] flex items-center ">
            <div
              onClick={() => logout()}
              className="cursor-pointer">
              <LogOut />
            </div>
          </div>
          <div
            dir='rtl'
            className="w-[50%] h-[70px] flex items-center ">
            <img src={userData?.imgurl}
              className="rounded-[50%] w-[60px] h-[60px] object-cover"
            />
            <div className="mr-[15px] text-[#000000b6] text-[12px] font-semibold">
              <p>{userData?.name}</p>
              <p>{userData?.username}</p>
            </div>
          </div>
        </nav>
        <img src={blogyimg}
          className="w-full h-[100vh] blur-[8px] "
        />
        <div className=" absolute w-full h-[100vh] top-0 right-0 left-0 bottom-0 bg-[#0000003d] "></div>
        <section className="w-[100%] h-[100vh] bg-[#0000002c] m-auto absolute top-0 px-[80px] flex items-end justify-center  ">
          <section
            dir='rtl'
            className="w-[90%] h-[85vh] text-[#000000b6]  ">
            <p className="font-semibold">مدیریت پنل</p>
            <div className="w-[58%] mt-[10px] flex justify-between">
              <button
                onClick={() => setTab(1)}
                className={`${tab === 1 ? "bg-[#3c3a3a69] w-[150px] h-[40px] rounded-[8px] text-[#ffffffdc] text-[12px] hover:bg-[#3c3a3a69]" : "bg-[#D5D5D5] w-[150px] h-[40px] rounded-[8px] text-[12px] hover:bg-[#3c3a3a69]"} `}>ویرایش اطلاعات کاربر </button>
              <button
                onClick={() => setTab(2)}
                className={`${tab === 2 ? "bg-[#3c3a3a69] w-[150px] h-[40px] rounded-[8px] text-[#ffffffdc] text-[12px] hover:bg-[#3c3a3a69]" : "bg-[#D5D5D5] w-[150px] h-[40px] rounded-[8px] text-[12px] hover:bg-[#3c3a3a69]"} `}> ایجاد مقاله جدید </button>
              <button
                onClick={() => setTab(3)}
                className={`${tab === 3 ? "bg-[#3c3a3a69] w-[150px] h-[40px] rounded-[8px] text-[#ffffffdc] text-[12px] hover:bg-[#3c3a3a69]" : "bg-[#D5D5D5] w-[150px] h-[40px] rounded-[8px] text-[12px] hover:bg-[#3c3a3a69]"} `}> ویرایش مقاله </button>
              <button
                onClick={() => setTab(4)}
                className={`${tab === 4 ? "bg-[#3c3a3a69] w-[150px] h-[40px] rounded-[8px] text-[#ffffffdc] text-[12px] hover:bg-[#3c3a3a69]" : "bg-[#D5D5D5] w-[150px] h-[40px] rounded-[8px] text-[12px] hover:bg-[#3c3a3a69]"} `}>مقاله های من</button>
            </div>
            <section className={`${tab === 1 ? "" : "hidden"}`}>
              <EditUser />
            </section>
            <section className={`${tab === 2 ? "" : "hidden"}`}>
              <CreateArticle />
            </section>
            <section className={`${tab === 3 ? "" : "hidden"}`}>
              <EditArticle />
            </section>
            <section className={`${tab === 4 ? "" : "hidden"}`}>
              <MyArticles tab={tab} setTab={setTab} />
            </section>
          </section>
        </section>
      </section>
    </>
  )
}
